.p-about > article {
  margin: 20px 0;
}

.p-about > article h1,
.p-about > h2 {
  text-align: center;
  font-size: 28px;
}
.p-about img {
  max-width: 120px;
  border-radius: 50%;
  border: 5px solid #64b18d;
  margin: 15px 0;
}
.p-about > article p,
.p-about > .c-quote {
  font-size: 16px;
  text-align: center;
  margin-top: 20px;
  color: rgba(0, 0, 0, 0.77);
}
