.popover-userIcon:hover{
  cursor: pointer;
}
.popover-userAction:before{
  -webkit-transform: rotate(45deg) translate(6px,6px);
  transform: rotate(45deg) translate(6px,6px);
  box-shadow: -1px -1px 1px -1px rgba(0,0,0,.54);
  content: '';
  display: block;
  width: 14px;
  position: absolute;
  z-index: 1000;
  height: 14px;
  background: #fff;
}
.popover-userAction{
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 1px 2px rgba(0,0,0,.25), 0 0 1px rgba(0,0,0,.35);
  width: 260px;
  position: absolute;

  z-index: 100;
  border-top-right-radius: 0;
}

.popover-userAction ul{
  list-style-type: none;
  padding: 10px 0;
}

.popover-userAction ul li{
  padding: 10px 20px;
  text-align: left;
}

.popover-userAction ul li a{
  width: 100%;
  display: block;
  padding: 0;
}

.popover-userAction.usr-drd{
  right: 6px;
  top: 46px;
}

.popover-userAction.usr-drd:before, .popover-userAction.usr-n:before{
  top: -15px;
  right: 3px;
}

.popover-userAction.usr-n{
  right: 2px;
  top: 36px;
}


@media screen and (max-width: 728px){
  .nav-top-bar>nav{
    height: 55px!important;
  }
  .popover-userAction.usr-drd:before{
    right: 24px;
  }
  .popover-userAction.usr-n:before{
    right: 79px;
  }
  .popover-userAction{
    width: 100%;
    top: 56px !important;
    position: fixed;
    height: auto;
    right: 0 !important;
  }
  #ThLogo img.logo-x{
    display: inline-block;
    vertical-align: middle;
    height: 36px;
  }
  #ThLogo img.logo-l{
    display: none;
  }
  #ThLogo h2{
    font-size: 14px;
    vertical-align: middle;
    margin-left: 10px;
    padding-left: 0;
    border:0;
  }
}

