.modal {
    overflow-x: auto;
    overflow-y: auto;
    bottom: 0;
    left: 0;
    outline: 0;
    position: fixed;
    right: 0;
    top: 0;
}

.modal-backdrop {
  background-color: #fff;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  opacity: .5;
}

.modal--overlay{
