  box-shadow: -1px -1px 1px -1px rgba(0,0,0,.54);
  content: '';
  display: block;
  position: absolute;
  top: 4px;
  left: 50%;
  width: 14px;
  height: 14px;
  background: #fff;
}

#s-tooltipOverlay{
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  display: block;
}

.s-l-tooltip{
  position: absolute;
  top: 38px;
  left: -54px;
  padding: 10px 0;
  background: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 4px;
  border-radius: 4px;
  z-index: 10;
  width: 130px;
  border: 1px solid #ccc;
}
ul.s-l-tooltip>li, ul.s-l-tooltip>span{
  text-align: left;
  padding: 8px 15px;
  display: block;
  color: rgba(0, 0, 0, 0.54);
  cursor: pointer;
}

